import React from "react";
import { motion } from "framer-motion";
import FilmSection from "../../sections/FilmSection";
// import { useTheme } from "@material-ui/core";

const Film = () => {
    // const theme = useTheme()

    return (
        <motion.main
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{
                type: "spring",
                stiffness: 260,
                damping: 20,
            }}
        >
            <FilmSection />
        </motion.main>
    );
};

export default Film;
