import React from "react";
import SectionContainer from "../../containers/SectionContainer";
import Gallery from "../../components/Gallery";
import { useTranslation } from "react-i18next";
import { makeStyles, useTheme, useMediaQuery } from "@material-ui/core";
import { Canvas } from '@react-three/fiber';
import { Asset3D, AssetType, Camera3D } from "../../components/Asset3D";

const FilmSection = () => {
    const { t } = useTranslation();
    const classes = useStyles();
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

    let settings = {};

    if(isMobile) {
        settings.transform = {
            position: [0.0, 0.4, -1.5],
            rotation: [0, -Math.PI/2, 0],
            scale: 0.03,
            rotationFactor: Math.PI / 2,
        };
    } else {
        settings.transform = {
            position: [0.04, 0.4, -1.5],
            rotation: [0, -Math.PI/2, 0],
            scale: 0.04,
            rotationFactor: Math.PI / 2, // scrollRotationFactor = Math.PI * 2 for full scroll
        };
    }

    return (
        <>
        <div className={classes.canvasContainer}>
            <Canvas camera={{ position: [0, 0, 5] }} >
                <Camera3D fov={10} zoom={1}/> 

                <directionalLight position={[1, 1, 1]} intensity={10} castShadow={false}/>

                <Asset3D type={AssetType.Film} position={settings.transform.position} rotation={settings.transform.rotation} scale={settings.transform.scale} scrollRotationFactor={settings.transform.rotationFactor} />

                {/* <OrbitControls/>*/}

                {/* Canvas Statistics */} 
                {/*<Stats />*/} 
                
            </Canvas>
        </div>

        <SectionContainer id="film_section" title={t('menu_film_section')}>
        <Gallery type={"film"} />
        </SectionContainer>
        </>
    );
};

const useStyles = makeStyles((theme) => ({
    canvasContainer: {
        position: "relative",
        height: "200vh",
        width: "100%",
        pointerEvents: "auto",
        marginTop: "0vh",
        [theme.breakpoints.down("sm")]: {
            marginTop: "25vh",
        },
        marginBottom: "-75vh",
        //zIndex: -1,
        //backgroundColor: "red",
    },
}));

export default FilmSection;
